.app {
  display: flex;
  min-width: 750px;
  height: 100vh;

  &__sidebar,
  &__content {
    padding-top: 20px;
    padding-bottom: 35px;
    height: inherit;
    overflow: auto;
  }

  &__sidebar {
    width: 260px;
    padding-right: 25px;
    padding-left: 35px;
    flex-shrink: 0;
  }

  &__content {
    flex-grow: 1;
    max-width: 1015px;
  }
}
